
html,
.container,
.imgbox {
  width: 100%;
  background-color: #ff3a1e;
}
body {
  margin: 0;
}
.imgbox {
  position: relative;
}
.share {
  width: 0.8rem;
  height: 0.8rem;
  position: absolute;
  top: 0.3rem;
  right: 0.4rem;
}
.imgbox .bgc {
  display: block;
  width: 100%;
}
.acTime {
  margin: 0.1rem auto;
  width: 6rem;
  height: 0.62rem;
  background-color: #f02a0f;
  line-height: 0.62rem;
  text-align: center;
  color: #fff;
  border-radius: 0.3rem;
}
.coupon {
  display: block;
  width: 6.9rem;
  height: 2rem;
  margin: 0.3rem auto;
}
.rule {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 0.4rem 0.35rem;
  color: #fff;
}
.rule > .title {
  position: relative;
  font-family: FZY4JW--GB1-0;
  font-size: 0.3rem;
  color: #fff;
  letter-spacing: -0.004rem;
  line-height: 0.42rem;
}
.rule > .title > span {
  width: 1.2rem;
  height: 0.12rem;
  background-color: #fbbb67;
  opacity: 0.5;
  position: absolute;
  bottom: 0.07rem;
  left: 0;
}
.rule > .ruletext {
  width: 100%;
}
.rule > .ruletext > p {
  font-family: FZY3JW--GB1-0;
  font-size: 0.28rem;
  color: #fff;
  letter-spacing: -0.0037rem;
  line-height: 0.42rem;
}
.toastdiv {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.7);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.toastdiv > .imgdiv {
  width: 5.9rem;
  height: 4.66rem;
  position: relative;
}
.toastdiv > .imgdiv > img {
  width: 100%;
  height: 100%;
}
.toastdiv > .imgdiv > .close {
  width: 0.8rem;
  height: 0.8rem;
  text-align: center;
  line-height: 0.8rem;
  position: absolute;
  top: 0;
  right: 0.1rem;
  font-size: 0.5rem;
  color: #999;
}
.sharediv {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2.88rem;
  border-radius: 0.4rem;
  background-color: #fff;
}
.sharediv > img {
  width: 100%;
  height: 100%;
}
.sharediv > .shareBtn {
  width: 100%;
  height: 2rem;
  position: absolute;
  top: 0;
  left: 0;
}
.sharediv > .closeshare {
  width: 100%;
  height: 0.88rem;
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 0.01rem solid #eee;
}